$primary:#AA0601;
$secondary:#DEE2E6;


/* 處理container RWD */
.container{
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
    @media (max-width:992px) {
        max-width: 914px;
    }
    @media (max-width:767px) {
        max-width: 737px;
        padding: 0 7.5px;
    }
}
/*
header 區塊 PC版
*/
.header{
    background-color: $primary;
    display: flex;
    justify-content: space-between;
    padding: 30px 88px;
}
.menu{
    display: flex;
    justify-content: end;
    align-items: center;
}
.menu-item{
    min-width: 80px;
    margin-right: 64px;
    a{
        color: #fff;
        font-size: 20px;
        text-decoration: none;
        display: block;
        line-height: 30px;
        &:hover{
            color: $secondary;
        }
    }
}

/*
header 區塊 Tablet版
*/
@media (max-width:992px) {
    .header{
        padding: 30px 39px;
    }
    .menu-item{
        margin-right: 40px;
    }
}
.menu-item-last{
    margin-right: 0px;
}
/*
header 區塊 Mobile版
*/
@media (max-width:767px){
    .header{
        flex-direction: column;
        text-align: center;
        padding: 0;
    }
    .bannerimg{
        padding: 10px 0;
    }
    .menu{
        flex-wrap: wrap;
    }
    .menu-item{
        margin: 0;
        text-align: center;
        border: 1px solid #fff;
        width: 50%;
        a{
            line-height: 45px;
            padding: 8px 0;
        }
    }
}
/*
footer區塊 PC版
*/
.footer{
    background-color:$primary;
}

.footerinfo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 1px solid #FFF;
}
.footer-menu{
    display: flex;
}
.footer-menu-item{
    margin-right: 48px;
    a{
        color: #fff;
        text-decoration: none;
        line-height: 24px;
        &:hover{
            color: $secondary;
        }
    }
    
}
.soical{
    display: flex;
    align-items: center;
    margin: 32px 0 24px 0;
    li{
        margin-left: 32px;
    }
}
.contactinfo{
    padding-bottom: 21px;
    li{
        img{
            height: 36px;
        }
        a{
            padding-left: 22px;
            color:#fff;
            font-size: 36px;
            line-height: 54px;
            text-decoration: none;
        }
    }
}
.copyright{
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        color: #fff;
        line-height: 24px;
        padding: 20px 0 27px 0;
    }
}
.copyright-zh{
    display: flex;
}
.copyright-zh-item{
    a{
        color: #fff;
        text-decoration: none;
        padding-left: 48px;
        line-height: 21px;
        font-size: 14px;
        &:hover{
            color: $secondary;
        }
    }
    
}
/*
footer區塊 Tablet版
*/
@media (max-width:992px){
    .footer-menu-item{
        margin-right: 40px;
    }
    .soical-item{
        img{
            height: 24px;
        }
    }
    .contactinfo{
        padding-bottom: 16px;
        width: 100%;
        li{
            img{
                height: 18px;
            }
            a{
                padding-left: 11px;
                font-size: 20px;
                line-height: 30px;
            }
        }
    }
    .copyright-zh-item{
        a{
            padding-left: 24px;
        }
        
    }
}
/*
footer區塊 Mobile版
*/
@media (max-width:767px) {
    .footerinfo{
        flex-direction: row-reverse;
    }
    .footer-menu{
        display: none;
    }
    .contactinfo{
        width: 59%;
        padding-top: 25px;
        li{
            a{
                padding: 0 0 0 7.5px;
                font-size: 16px;
            }
        }
    }
    .soical{
        margin-top: 0;
        li{
            margin-left: 24px;
        }
    }
    li.first{
        margin-left: 0;
    }
    .copyright{
        flex-direction: column;
        align-items: flex-start;
        p{
            padding-bottom: 8px;
        }
        padding-bottom: 16px;
    }
    .copyright-zh{
        flex-direction: column;
    }
    .copyright-zh-item{
        padding: 0 0 8px 0;
        a{
            padding: 0;
        }
    }
}